/* src/index.css */

/* --- 1. GLOBAL & CENTERING FIX --- */
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  margin: 0;
  background-color: #282c34; /* Dark background */
  color: white;
}

/* THIS IS THE FIX:
  We make the main #root div the flex container 
  that centers everything.
*/
#root {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
}


/* --- 2. GAME & BOARD STYLES --- */

/* Main game wrapper */
.game-container {
  text-align: center;
}

/* Title style */
.game-title {
  font-size: 3rem;
  margin-bottom: 10px;
  color: #61dafb; /* React blue color */
}

/* Designer credit style */
.game-designer {
  font-size: 0.9rem;
  font-style: italic;
  color: #999;
  margin-top: 20px;
}

/* Status message style */
.status {
  margin-bottom: 15px;
  font-size: 1.5rem; /* Bigger text */
  font-weight: bold;
  height: 30px; /* Set height to prevent layout shift */
}

/* Board row */
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

/* --- 3. SQUARE STYLES --- */
.square {
  background: #3b3f47; /* Darker square */
  border: none; /* Remove old border */
  border-radius: 8px; /* Rounded corners */
  float: left;
  font-size: 3rem; /* Much bigger X's and O's */
  font-weight: bold;
  line-height: 80px; /* Center text vertically */
  height: 80px; /* Bigger button */
  width: 80px; /* Bigger button */
  margin: 5px; /* Add spacing between squares */
  padding: 0;
  text-align: center;
  cursor: pointer;
  color: white; /* Make 'X' and 'O' white */
  transition: background-color 0.2s ease-in-out, transform 0.1s ease;
}

.square:hover {
  background: #4a4f58; /* Lighter on hover */
}

.square:active {
  transform: scale(0.95); /* Click effect */
}

.square:focus {
    outline: none;
}


/* --- 4. MODAL STYLES --- */
.modal-overlay {
  /* Sits on top of everything */
  position: fixed; 
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  
  /* Dark semi-transparent background */
  background: rgba(0, 0, 0, 0.7); 
  
  /* Center the content */
  display: flex;
  justify-content: center;
  align-items: center;

  /* Nice fade-in effect */
  animation: fadeIn 0.3s ease;
}

.modal-content {
  background: #3b3f47;
  padding: 30px 40px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  color: white;
  
  /* Pop-in effect */
  transform: scale(0.9);
  animation: zoomIn 0.3s ease forwards;
}

.modal-content h2 {
  font-size: 2.5rem;
  margin-top: 0;
  color: #61dafb; /* React blue */
}

.modal-content button {
  background: #61dafb;
  color: #282c34;
  border: none;
  padding: 12px 25px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.modal-content button:hover {
  background: #ffffff;
  transform: scale(1.05);
}

/* Keyframe animations for the effects */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes zoomIn {
  from { transform: scale(0.9); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}